.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.addTask {
  height: 30vh;
  background-color: grey;
  width: 100%;
  display:flex;
  justify-content: center;
  align-items: center;
}
.addTask input{
  height: 30px;
  padding-right: 22px;
}
.addTask button{
 
  height: 35px;
  background-color: #61dafb;
}
.list{
  height: auto;
  width: 100%;
  display: flex;
  align-items: center;
  padding-top: 50px;
  flex-direction: column;
}

.task {
  width:  500px;
  height:  50px;
  display:  flex;
  margin: 15px;
  display:  flex;
  align-items: center;
  justify-content: center;
}
.task button{
  width:  35px;
  height:  24px;
  margin-left: 20px;
  text-align: center;
  background-color: #61dafb;
}